.backP {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    background-color: #ffffffeb;
}

.back {
    width: 1cm;
    border-radius: 50%;
    /* background-color: blue; */
}

.mainTutor {
    width: 100rvw;
}

.mainTutorR1 {
    height: 50vh;
    padding: 0.5cm 1cm;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    /* border:thin solid rgb(180, 180, 180); */
    /* background-image: url("bg.jpg"); */
    /* background-size: cover;
    background-repeat: no-repeat;
    background-position: center; */
    background-color: #cdb4db;
}

.mainTutorImg {
    width: 8cm;
    border-radius: 50%;
}

.mainTutorBox {
    display: flex;
    flex-direction: column;
    row-gap: 0.8cm;
    justify-content: center;
}

.mainTutor1Text {
    display: flex;
    flex-direction: column;
    row-gap: 0.5cm;
    justify-content: center;
    align-items: center;
}

.starP {
    display: flex;
    flex-direction: row;
    column-gap: 0.3cm;
    align-items: center;
}

.star1 {
    width: 1cm;
    height: auto;
}

.starCount {
    font-size: 20px;
}

.subjectDiv {
    display: flex;
    flex-direction: row;
    align-items: center;
    column-gap: 0.5cm;
    row-gap: 0.5cm;
}

.subjectC {
    padding: 0.8cm 0.5cm;
    align-items: center;
    background-color: #ffc8dd;
    border-radius: 25px;
    width: 8cm;
    color: #5a1c00;
    font-weight: bolder;
    font-size: 20px;
    height: auto;
}

.reviewC {
    padding: 0.8cm 0.5cm;
    align-items: center;
    background-color: #bde0fe;
    border-radius: 25px;
    width: 8cm;
    color: #006760;
    font-size: 20px;
    font-weight: bolder;

    height: auto;
}

.courseDiv {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    row-gap: 0.5cm;
    column-gap:0.5cm;
    color:white;
  }
  
  .courseCard {
    width:8cm;
    height:4cm;
    margin: 0 15px 30px;
    overflow: hidden;
    border-radius: 28px;
    padding: 30px 20px;
    background-color: #000000;
    position: relative;
  }
  

  .courseCard:hover .courses-item_bg {
    -webkit-transform: scale(10);
    -ms-transform: scale(10);
    transform: scale(10);
    box-shadow: 0.5px 5px 0.5px 0.5px rgba(128, 128, 128, 0.844);
  }
  
  .courses-item_bg {
    height: 128px;
    width: 128px;
    background-color: #ffcd29;
    z-index: 1;
    position: absolute;
    top: -75px;
    right: -75px;
    border-radius: 50%;
    transition: all .2s ease;
  }
  
  .red{
    background-color: #d51919;
  }
  .cardCont{
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 0.5cm;
  }

  .cardCont{
    z-index:5;
    position: relative;
  }

  .courseName{
    font-size:25px;
    font-weight: bold;
  }

  .courseDate{
    font-size: 20px;
  }